@import "variable";

.button {
  margin-top: 16px;
  display: block;
  font-size: 14px;
  line-height: 1;
  color: white;
  background-color: $link-color;
  // cursor: pointer;
  transition: background-color;
  padding: 19px 62px;
  // text-transform: uppercase;
  border-radius: 10px;
  border: none;
  width: 413px;
  box-shadow: 0 6px 23px rgba(0, 117, 255, 0.46);
}

@media screen and (max-width: 1000px) {
  .button {
    width: 280px;
  }
}
